/* src/components/ChestViewer/ChestViewer.css */

.chest-viewer {
  padding: 40px;
  display: flex;
  gap: 40px;
  align-items: flex-start;
}

/* Image viewer */
.chest-image-container {
  max-width: 200px;
  max-height: 200px;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

/* Text panel */
/* Text panel */
.chest-text-container {
  width: 200px;
  height: 200px;
  overflow-y: auto;

  display: flex;
  align-items: center;      /* vertical centering */
  justify-content: center;  /* horizontal centering */
}
